/* animation */
@keyframes vibrate-box-animation {
  0% {
    transform: translateX(-10px);
  }
  25% {
    transform: translateX(10px);
  }
  50% {
    transform: translateX(-10px);
  }
  75% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}

.vibrate-box {
  animation: vibrate-box-animation 0.2s ease;
  animation-fill-mode: forwards;
}
